import Vue from 'vue'
import Router from 'vue-router'

import register from '@/components/register'
import login from '@/components/login'
import dashboard from '@/components/dashboard'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '*',
      redirect: '/login'
    },
    {
      path: '/',
      component: login
    },
    {
      path: '/register',
      component: register
    },
    {
      path: '/login',
      component: login
    },
    {
      path: '/dashboard',
      component: dashboard,
      meta: { 
        requiresAuth: true
      }
    }
  ]
})

export default router;