import axios from 'axios';

const url = 'api/challs/';

class challenge_manage {
    static get_challs() {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(url);
                const data = res.data;
                resolve(data.map(chall => ({...chall})))
            } catch(err) {
                reject(err);
            }
        })
    }
    static new_chall(name, category, content, key, value) {
        return axios.post(url, {
            name, category, content, key, value
        });
    }
}

export default challenge_manage;